import KonamiCode from './konamiCode';

const onKonamiEnter = () => {
  const pushInfoTag = document.querySelector('div#devInfo');
  const codeTag = document.querySelector('div#devInfo pre');

  codeTag.innerHTML = JSON.stringify(window.pushData, null, 2);

  const display = !pushInfoTag.style.display ||
    pushInfoTag.style.display === 'none' ? 'block' : 'none';

  pushInfoTag.style.display = display;
};

export default () => {
  new KonamiCode(onKonamiEnter).activate();
};
