import { title } from 'components/Title';
import button from 'components/Button';
import Listed from 'components/Views/Listed';
import Suggestion from 'components/Views/Suggestion';

import './style.scss';

export default info => (`
  <div class="step active">
    <div class="header">
      ${title(`${info.Asset} - ${info.StructureName} <br /> Encerramento no dia ${info.formattedMaturityDate}`)}
    </div>
    <div class="content">
      ${info.IsIncomingIdea ? Suggestion(info) : Listed(info)}
    </div>
    <div class="footer">
      <div class="button-group">
        ${button('cancel', 'Recusar')}
        ${button('confirm', 'Reservar')}
      </div>
    </div>
  </div>
`);
