class KonamiCode {
  constructor(onKonamiEnter) {
    this.onKonamiEnter = onKonamiEnter;
    this.startTime = 0;
    this.startX = 0;
    this.startY = 0;
    this.konami = ['up', 'up', 'down', 'down',
      'left', 'right', 'left', 'right', 'b', 'a'];
    this.current = [];
  }

  onTouchstart(e) {
    this.startTime = e.timeStamp;
    this.startX = e.touches[0].clientX;
    this.startY = e.touches[0].clientY;
  }

  onMovement(direction) {
    this.current.push(direction);

    if (this.current.length > this.konami.length) {
      this.current = [];
    }

    for (let i = 0; i < this.current.length; i += 1) {
      if (this.current[i] !== this.konami[i]) {
        this.current = [];
        return;
      }
    }

    if (this.current.length < this.konami.length) {
      return;
    }

    this.current = [];
    try {
      this.onKonamiEnter();
    } catch (e) {
      // eslint-disable-next-line consistent-return
      return 0;
    }
  }

  onTouchend(e) {
    const elapsed = e.timeStamp - this.startTime;

    if (elapsed > 1000) {
      return;
    }

    const touch = e.changedTouches[0];
    const difX = (this.startX - touch.clientX) / window.innerWidth;
    const difY = (this.startY - touch.clientY) / window.innerHeight;
    const aDifX = Math.abs(difX);
    const aDifY = Math.abs(difY);

    if (aDifX > aDifY * 2) {
      this.onMovement(difX > 0 ? 'left' : 'right');
    }

    if (aDifY > aDifX * 2) {
      this.onMovement(difY > 0 ? 'up' : 'down');
    }

    if (aDifX < 0.1 && aDifY < 0.1 && elapsed < 400) {
      this.onMovement(this.current.indexOf('b') < 0 ? 'b' : 'a');
    }
  }

  activate() {
    window.addEventListener('touchstart', e => this.onTouchstart(e), false);
    window.addEventListener('touchend', e => this.onTouchend(e), false);
  }
}

export default KonamiCode;
