import mock from 'mock';
import Store from 'actions/Store';
import Interaction from 'actions/Interaction';
import errorAction from 'actions/errorAction';

import initApp from './initializer';
import showDevInfo from '../../helpers/utils/showDevInfo';

window.mock = JSON.stringify(mock);

export default () => {
  window.UpdateValues = (payload) => {
    const parseValue = JSON.parse(payload);
    const store = new Store(parseValue);

    store.then((storeData) => {
      initApp(storeData);

      window.pushData = storeData;

      const interaction = new Interaction(storeData); // eslint-disable-line
    });
  };

  showDevInfo();
  errorAction();
};
