
import './style.scss';

export default (titles, performances) => (`
  <div class="table">
    <div class="table__titles">
      ${titles.map(title => (`<p class="table__title">${title}</p>`)).join('')}
    </div>
    ${performances.map(performance => (`
      <div class="table__content">
        <p class="table__text">${performance.AssetPerformance}</p>
        <p class="table__text">${performance.OperationPerformance}</p>
      </div>
    `)).join('')}
  </div>
`);
