import { $qs } from 'helpers/utils/selector';
import errorApi from '../components/ErrorApi';

export default () => {
  const loadingBox = $qs('.showbox');
  setTimeout(() => {
    if (loadingBox) {
      loadingBox.innerHTML = errorApi();

      const errorButton = $qs('.error-page .button--cancel');

      if (errorButton) {
        errorButton.addEventListener('click', () => {
          document.location = 'app://?state=refused&edited=true&data={}';
        });
      }
    }
  }, 8000);
};
