import './style.scss';

export default legs => (`
  <div class="structure">
    ${legs.map(leg => (
    `<div class="structure__item">
      <div class="structure__index">${leg.index}</div>
      <div class="structure__content">
      <p class="structure__text">${leg.formattedSide} ${leg.formattedType}</p>
      <p class="structure__text">Strike ${leg.formattedStrike}</p>
      ${leg.Barrier ? `<p class="structure__text">${leg.Barrier} ${leg.formattedStrikeBarrier}</p>` : ''}
      ${leg.Rebate ? `<p class="structure__text">Rebate ${leg.formattedRebate}</p>` : ''}
      </div>
    </div>`
  )).join('')}
  </div>
`);
