import { $qs } from 'helpers/utils/selector';

class Interaction {
  constructor(data) {
    this.data = data;
    this.actions();
  }

  preparePayload() {
    const payload = this.data;

    const data = {
      HashCode: payload.HashCode,
      InteractiveOperationId: payload.InteractiveOperationId,
      OperationId: payload.OperationId,
      OrderId: payload.OrderId,
      Account: payload.Account,
      AcceptedQuantity: payload.Quantity,
      DeviceAddress: '',
    };

    return data;
  }

  send(state) {
    const data = state === 'approved' ? this.preparePayload() : {};
    document.location = `app://?state=${state}&edited=true&data=${JSON.stringify(data)}`;
  }

  actions() {
    const cancelButton = $qs('.button--cancel');
    const buyButton = $qs('.button--confirm');

    cancelButton.addEventListener('click', () => {
      this.send('refused');
    });

    buyButton.addEventListener('click', () => {
      this.send('approved');
    });
  }
}

export default Interaction;
