import axios from 'axios';
import converDate from 'helpers/utils/convertDate';
import convertToBRL from 'helpers/utils/convertToBRL';
import convertToPercentage from 'helpers/utils/convertToPercentage';

class Store {
  constructor(data) {
    this.data = {};

    return this.normalizeData(data);
  }

  async normalizeData(data) {
    const promise = new Promise((resolve) => {
      const formatted = {
        formattedMaturityDate: converDate(data.MaturityDate),
        formattedAssetPrice: convertToBRL(data.AssetPrice),
        totalFinance: convertToBRL(data.AssetPrice * data.Quantity),
        totalClientCost: data.ClientCost * data.Quantity,
        formattedTotalClientCost: convertToBRL(Math.abs(data.ClientCost * data.Quantity)),
        formattedLegs: data.Leg.map((leg, index) => (Object.assign({}, leg, {
          formattedSide: this.getFormattedSide(leg.Side),
          formattedType: this.getFormattedType(leg.Type, data.Asset),
          formattedStrike: data.IsIncomingIdea ?
            convertToPercentage(leg.Strike) : convertToBRL(leg.Strike),
          index: leg.Type.indexOf('Stock') > -1 ? '-' : index + 1,
          formattedStrikeBarrier: leg.StrikeBarrier ? convertToPercentage(leg.StrikeBarrier) : 0,
          formattedRebate: leg.Rebate ? convertToBRL(leg.Rebate) : 0,
        }))),
      };

      let newData = Object.assign({}, data, formatted);

      if (data.IsIncomingIdea) {
        const formattedSuggestion = {
          formattedMaxProfit: !data.Suggestion.MaxProfit ? 'Ilimitado' : convertToBRL(data.Suggestion.MaxProfit * data.Quantity),
          formattedMaxLoss: !data.Suggestion.MaxLoss ? 'Ilimitado' : convertToBRL(data.Suggestion.MaxLoss * data.Quantity),
          SuggestionPerformance: data.Suggestion.SuggestionPerformance.map(item => ({
            AssetPerformance: item.AssetPerformance,
            OperationPerformance: convertToBRL(item.OperationPerformance * data.Quantity),
          })),
        };
        newData.Suggestion = Object.assign({}, data.Suggestion, formattedSuggestion);
      }

      this.getGraphImage(data.ImagePath)
        .then((result) => {
          newData = Object.assign({}, newData, { graph: result });
        })
        .then(() => {
          this.data = newData;
          resolve(newData);
        });
    });

    return promise;
  }

  // eslint-disable-next-line class-methods-use-this
  async getGraphImage(imagePath) {
    const svg = await axios({
      method: 'get',
      url: imagePath,
      timeout: 4000,
    })
      .then(response => (response.data))
      .catch(() => (false));

    return svg;
  }

  // eslint-disable-next-line class-methods-use-this
  getFormattedSide(side) {
    if (side === 'C' || side === 'V') {
      return side === 'C' ? 'Compra' : 'Venda';
    }
    return '';
  }

  // eslint-disable-next-line class-methods-use-this
  getFormattedType(type, asset) {
    if (type.indexOf('Option') > -1) { return type.replace(/Option/g, '').trim(); }

    if (type.indexOf('Stock') > -1) { return asset; }

    return type;
  }

  get get() {
    return this.data;
  }
}

export default Store;
