import './style.scss';

const renderDescriptionItem = text => (
  `<p class="box__description">${text}</p>`
);

const renderDescription = description => (
  Array.isArray(description) ?
    `<div class="box__map">
      ${description.map(item => (renderDescriptionItem(item))).join('')}
    </div>` :
    renderDescriptionItem(description)
);

export default args => (`
  <div class="box box--${args.customClass}">
    ${args.title ? `<p class="box__title">${args.title}</p>` : ''}
    ${renderDescription(args.description)}
  </div>
`);
